.side-navbar {
    // background-color: aquamarine;
    height: calc(100vh - var(--body-padding) * 2);
    padding: 1.4rem 0;
    border-right: 1px solid var(--border-color-primary);
    min-width: 6rem;


    &__brand-logo {
        padding: 0 1.7rem 1.1rem 1.6rem;
        display: flex;
        justify-content: center;
        margin: 0 auto;
        position: relative;
        // border-width: 3pxz;

        &::after{
            content: "";
            position: absolute;
            top: 100%;
            left: 50%;
            width: 3.2rem;
            height: 1px;
            background-color: var(--border-color-primary);
            transform: translateX(-50%);
        }
    }

    &__project-links {
        display: flex;
        flex-direction: column;
        gap: 1.6rem;
        padding: 1rem 0;
        list-style: none;
        width: 100%;


        & li {
            position: relative;
            width: 100%;
            display: flex;
            flex-direction: column;
            align-items: center;
            & a {
                background-color: #F5F5F5;
                padding: .6rem;
                border-radius: var(--br-sm);
                display: flex;
                justify-content: center;
                align-items: center;
                width: fit-content;
                // min-width: 28px;
                width: 3.2rem;
                height: 3.2rem;

                & svg{
                    width: 18px;
                    height: 18px;
                }

                & svg:active{
                    fill: inherit;
                }


                &.active {
                    border: 1px solid #533BE5;
                    background:rgba(83, 59, 229, 0.05);
                    &::after {
                        content: "";
                        position: absolute;
                        top: 50%;
                        left: 0;
                        width: 2px;
                        background-color: #533BE5;
                        transform: translateY(-50%);
                        height: 80%;
                        border-top-right-radius: var(--br-lg);
                        border-bottom-right-radius: var(--br-lg);
                    }
                }
            }
        }
    }
}